import React from "react";
import styled, { css, keyframes } from "styled-components";
import theme from "../theme/theme";
import { Link } from "react-router-dom";
import SocialMedia from "../components/social-media";

import Photo1 from "../components/assets/background/photo1.jpg";
import Photo2 from "../components/assets/background/photo2.jpg";
import Photo3 from "../components/assets/background/photo3.jpg";
import Photo4 from "../components/assets/background/photo4.jpg";

import WpaLogo from "../components/assets/logo-wpa";

const slide = keyframes`
  0% {
    opacity: 1;
    background-image: url(${Photo1});
  }
  20% {
    opacity: 1;
    background-image: url(${Photo1});
  }
  20.1% {
    opacity: 1;
    background-image: url(${Photo2});
  }
  60% {
    opacity: 1;
    background-image: url(${Photo2});
  }
  60.1% {
    opacity: 1;
    background-image: url(${Photo3});
  }
  80% {
    opacity: 1;
    background-image: url(${Photo3});
  }
  80.1% {
    opacity: 1;
    background-image: url(${Photo4});
  }
  100% {
    opacity: 1;
    background-image: url(${Photo4});
  }
`;

const animation = props =>
  css`
    ${slide} ${props.animationLength} 20s infinite;
  `;

const HomePageContainer = styled.div`
  background-color: #222222;
  overflow: hidden;
  position: relative;

  text-align: center;
  width: 100vw;
  height: 100vh;
`;

const Overlay = styled.div`
  background: rgba(0,0,0, 0.5);
  width: 100%;
  height: 100%;

  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
`;

const Photos = styled.div`
  animation: ${animation};
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
`;

const HomeNavContainer = styled.div`
  margin: 0 auto;
  position: absolute;
`;

const LogoContainer = styled.div`
  width: 100%;
  height: auto;
`;

const Logo = styled(WpaLogo)`
  color: ${theme.PrimaryBgColor};
  width: 300px;
  height: auto;
  padding-bottom: 5px;
`;

const LinkContainer = styled.div`
  margin 10px auto 20px;
`;


const LinkStyled = styled(Link)`
  ${theme.AppearanceNone}
  ${theme.FontFamilyMedium};
  text-shadow: 0px 0px 5px rgba(0,0,0,0);
  color: ${theme.PrimaryBgColor};
  opacity: 70%;
  font-size: 16px;
  text-transform: uppercase;
  margin: 0 15px;

  &:hover {
    ${theme.FontFamilyMedium};
    color: ${theme.HighlightColor};
  }

  @media (max-width: 600px) {
    font-size: 80%;
  }
`;

const HomePage = () => {
  return (
    <HomePageContainer>
      <Photos>
      <Overlay>
      <HomeNavContainer>
        <LogoContainer>
          <Logo />
        </LogoContainer>
          <LinkContainer>
            <LinkStyled to="/about">About</LinkStyled>
            <LinkStyled to="/instructors">Instructors</LinkStyled>
            <LinkStyled to="/imta">IMTA</LinkStyled>
            <LinkStyled to="/faq">FAQ</LinkStyled>
            <LinkStyled to="/contact">Contact</LinkStyled>
          </LinkContainer>
        <div>
          <SocialMedia />
        </div>


      </HomeNavContainer >
      </Overlay>
      </Photos>
    </HomePageContainer>

  );
}

export default HomePage;