import React, { useState } from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import Modal from "react-modal";

import theme from "../theme/theme";
import Pages from "../components/pages"
import Title from "../components/title";

import ClientPdfs from "../utils/client-pdfs";


const CARD_SPACING = 5;
const MainContainer = styled.div`
  display: flex;
  flex-flow: column wrap;
`;

const CardContainer = styled.div`
  // Hack to make sure cards align properly with Page.Lane
  margin: ${CARD_SPACING * -1}px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
`;

const Card = styled.div`
  box-sizing: border-box;
  padding: ${CARD_SPACING}px;
  position: relative;
  width: 25%;


  @media (max-width: 600px) {
    width: 50%;
  }
`;

const Overlay = styled.div`
  background: rgba(0,0,0, 0.5);
  background: linear-gradient(0deg, rgba(0,0,0,0.8) 0%, rgba(0,0,0,0) 30%);

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
`;

const CategoryPhoto = styled.img`
  object-fit: cover;
  width: 100%;
  height: 100%;
`;

const CategoryContainer = styled.div`
  position: relative;
`

const CategoryName = styled.div`
  ${theme.FontFamilyBold}
  color: ${theme.PrimaryColorOnDark};
  text-align: center;
  text-transform: uppercase;
  padding: 5px 5px;

  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 5px;
  box-sizing: border-box;

  width: 100%;

  &:hover {
    color: ${theme.HighlightColor};
    cursor: pointer;
  }

  @media (max-width: 600px) {
    font-size: 50%;
  }
`;

const PopUpTitle = styled.div`
  ${theme.FontFamilyBold}
  color: ${theme.PrimaryTextColor};

  padding: 30px 30px;
  font-size: 20px;
  text-align: center;
`;

const PdfNameContainer = styled.div`
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  width: 100%;
`;

const PdfName = styled.div`
  background-color: #F3F3F3;
  box-sizing: border-box;
  padding: 15px 5px 5px 5px;
  display: flex;
  flex-flow: row wrap;
  height: 90px;
  width: 100%;
  border-radius: 2px;
`;

const PdfNameText = styled.text`
  ${theme.FontFamilyBold}
  color: ${theme.PrimaryTextColor};
  text-transform: uppercase;
  text-align: center;
  display: block;

  width: 100%;
  height: 70%;

  @media (max-width: 700px) {
    font-size: 50%;
    overflow: hidden;
    word-wrap: break-word;
  }
`;

const LinkContainer = styled.div`
  text-align: center;

  width: 100%;
  height: 20%;
`;

const LinkStyled = styled(Link)`
  ${theme.AppearanceNone}
  ${theme.FontFamilyMedium}
  color: ${theme.HighlightColor};
  display: inline;
  font-size: 10px;
  text-transform: uppercase;

  &:hover {
      color: #D36161;
    }

    @media (max-width: 600px) {
    font-size: 50%;
  }
`;


Modal.setAppElement("#root");
const StudentPage = () => {
  const [genreObject, setGenreObject] = useState(null);
  const [pdfObject, setPdfObject] = useState(null);



  return (
    <Pages>
      <Pages.Lane>
          <MainContainer>
        <Pages.Section>
        <Title>Student Resources</Title>
            <CardContainer>
              {Object.keys(ClientPdfs).map((ageName, i) => (
                <Card
                  onClick={() => setGenreObject(ClientPdfs[ageName])}
                  key={i}>

                <CategoryContainer>
                  <Overlay />
                  <CategoryPhoto src={ClientPdfs[ageName].image} alt="ages"/>
                  <CategoryName onClick={() => setPdfObject(null)}>
                    {ClientPdfs[ageName].name}
                  </CategoryName>
                  </CategoryContainer>
                </Card>
              ))}
            </CardContainer>


              <PopUpTitle>{genreObject?.name}</PopUpTitle>
            <CardContainer>
              {genreObject &&  (
                <>
                  {Object.keys(genreObject.children).map((genreName, i) => (
                    <Card
                        onClick={() => setPdfObject(genreObject.children[genreName])}
                        key={i}>
                      <CategoryContainer>
                        <Overlay />
                        <CategoryPhoto
                          src={ClientPdfs.children.children[genreName].genre}
                          alt="genres"/>
                        <CategoryName>
                          {genreObject.children[genreName].name}
                        </CategoryName>
                      </CategoryContainer>
                    </Card>
                  ))}
                </>
              )}
            </CardContainer>


              <div>
                <div>
                  <PopUpTitle>{pdfObject?.name}</PopUpTitle>
                </div>
              </div>

            <CardContainer>
              {pdfObject &&  (
                <>
                  {pdfObject.children.map((pdf, i) => (
                    <Card key={i}>
                      <PdfNameContainer>
                      <PdfName>
                        <PdfNameText>
                          {pdf.name}
                        </PdfNameText>

                          <LinkContainer key={i}>
                            <LinkStyled
                              to={pdf.href} target="_blank" download>Download</LinkStyled>
                            &middot;
                            <LinkStyled to={pdf.href} target="_blank">View</LinkStyled>
                            </LinkContainer>
                            </PdfName>
                      </PdfNameContainer>
                    </Card>
                  ))}
                </>
              )}
            </CardContainer>
        </Pages.Section>
          </MainContainer>

      </Pages.Lane>
    </Pages>
  );
}

export default StudentPage;